	import { groupCollapsed } from "console";
	import Question from './Question.svelte';
	import Answer from './Answer.svelte';
	
	let currRank = -1;
	let gameStart = false;
	let win = false;
	let lose = false;
	export let ranks = [
		{ 
			name: "Porcelain",
			class: "tag small"
		},
		{ 
