		},
		{ 
			name: "Ruby",
			class: "tag small"
		},
		{ 
			name: "Bronze",
			class: "tag small"
		},
		{ 
			name: "Silver",
			class: "tag small"
		},
		{ 
			name: "Gold",
			class: "tag small"
		},
		{ 
			name: "Platinum",
			class: "tag large"
		}
	]
	let quizIdx = 0;	

	const quiz = [
		{
			question: 'What is the standard abbreviation for your Charisma score?',
			correctAnswer: 'CHA',
			answers: [
				'CHA',
				'DEX',
				'WIS',
				'CON'
			],
		},
		{
			question: 'This is question 2',
			correctAnswer: 'B',
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		},
		{
			question: 'This is question 3',
			correctAnswer: 'B',
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		},
		{
			question: 'This is question 4',
			correctAnswer: 'B',
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		},
		{
			question: 'This is question 5',
			correctAnswer: 'B',
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		},
		{
			question: 'This is question 6',
			correctAnswer: 'CHA',
			answers: [
				'CHA',
				'DEX',
				'WIS',
				'CON'
			],
		},
		{
			question: 'This is question 7',
			correctAnswer: 'B',
			answers: [
				'A',
