			],
		},
		{
			question: 'This is question 8',
			correctAnswer: 'B',
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		},
		{
			question: 'This is question 9',
			correctAnswer: 'B',
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		},
		{
			question: 'This is question 10',
			correctAnswer: 'B',
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		},

	]

	function startGame(){
		currRank += 1;
		gameStart = true;
	}

	function checkAnswerHandler(answerText, id){
		const isCorrect = answerText === quiz[quizIdx].correctAnswer;
		if (isCorrect) {
			document.getElementById(id).classList.add('green');
			currRank += 1;
			quiz[quizIdx].question = 'Correct!';
			setTimeout(() => {
				document.getElementById(id).classList.remove('green');
				quizIdx += 1;
			}, 2000)
		}
		else {
			quiz[quizIdx].question = 'Wrong!';
			document.getElementById(id).classList.add('red');
			let otherAnswers = document.getElementsByClassName('answer');
			for (let i = 0; i < otherAnswers.length; i++){
				if (otherAnswers[i].classList.contains(quiz[quizIdx].correctAnswer.split(' ').join('-'))){
					otherAnswers[i].classList.add('green');
				}
			}
		}
	}
</script>

<main>
	<div class="top">
		<img class="title" src="images/title-1.png" alt="title" />
		<div class="board">
			{#each ranks as rank, id}
				<div class="tag-area">
					{#if id === currRank}
					<div class="tag-contain curr"><div class={`${rank.class} curr-font`}>{rank.name}</div></div>
					{:else}
					<div class="tag-contain"><div class={rank.class}>{rank.name}</div></div>
					{/if}
				</div>
			{/each}
		</div>
	</div>
	<div class="bottom">
	{#if !(gameStart)}
		<div class="opener">Welcome to the game! Press Start To continue!</div>
		<button class="start" on:click={startGame}></button>
	{:else}
		<Question questionText={quiz[quizIdx].question}/>
		<div class="answers">
			{#each quiz[quizIdx].answers as answer, idx }
			<button class={`${answer.split(' ').join('-')} answer`} id={idx} on:click={checkAnswerHandler(answer, idx)} >
				<Answer class="ans" answerText={answer} {idx} />
			</button>
			{/each}	
		</div>
		{/if}
	</div>
</main>

